.card{
	position: relative;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;

	margin-bottom: 2em;
	padding-bottom: 1em;
}

.card__image, .card__figure{
	-ms-flex: 1 1 30%;
	    flex: 1 1 30%;
}

.card__image > img {
	width: 100%;
	height: auto;
	padding: 10px;
}

.card__body{
	-ms-flex: 2 1 80%;
	    flex: 2 1 80%;
}

.card__actions{
	-ms-flex: 1 1 40%;
	    flex: 1 1 40%;
}

.card__footer{
	position: absolute;
	left: 10px;
	right: 10px;
	top: 100%;

	min-height: 2em;
}


